import Vue from 'vue'
import Router from 'vue-router'
import layout from '@/components/layout'
import admin from '@/components/admin'
import question from '@/components/question'
import index from '@/components/index'

Vue.use(Router)

export default new Router({
  base: '/problem/',
  mode : 'history',
  routes: [
    {
      path: '/',
      component: layout,
      children: [
        {
          path: '/',
          name: 'index',
          component: index
        },
        {
          path : ':id',
          name : 'question',
          component : question
        }
      ]
    }
  ]
})



