<template>
    <div>
        <mu-card v-if="loaded">
            <mu-card-title :title="`第${cur}题`" subTitle="送命题"/>
            <mu-card-text>
                {{problems[cur-1].name}}
                <br><br>
                <mu-radio :label="problems[cur-1].A" name="group" nativeValue="A" v-model="curvalue" class="demo-radio"/> <br/>
            <mu-radio :label="problems[cur-1].B" name="group" nativeValue="B" v-model="curvalue"  class="demo-radio"/> <br/>
            <mu-radio :label="problems[cur-1].C" name="group" nativeValue="C" v-model="curvalue"  class="demo-radio"/> <br/>
            <mu-radio :label="problems[cur-1].D" name="group" nativeValue="D" v-model="curvalue"  class="demo-radio"/> <br/>
            
            </mu-card-text>
            
            <mu-card-actions>
                <mu-flexbox>
                    <mu-flexbox-item>
                    <mu-raised-button v-if="cur!=1" label="上一题" @click="prep()" primary fullWidth/>
                    
                    </mu-flexbox-item>
                    <mu-flexbox-item class="flex-demo">
                    <mu-raised-button v-if="cur!=problems.length" label="下一题" @click="next()" secondary fullWidth/>
                    <mu-raised-button v-else label="提交" @click="show_submit()" secondary fullWidth/>
                    
                    </mu-flexbox-item>
                </mu-flexbox>
            </mu-card-actions>

            <mu-snackbar v-if="snackbar" :message="snackbarmsg" action="关闭" @actionClick="hideSnackbar" @close="hideSnackbar"/>

            <mu-dialog :open="dialog" title="提交" @close="close">
                

                    请输入您的学号<br>
                    <mu-text-field v-if="!ZJUerror" type="number" hintText="3170...."  v-model="ZJUid" />
                    <mu-text-field v-else type="number" hintText="3170...." errorText="请输入合法的学号"  v-model="ZJUid" /><br/>    
                    <mu-flat-button slot="actions" @click="close" primary label="取消"/>
                    <mu-flat-button slot="actions" primary @click="submit()" label="确定"/>

                <!-- <div v-else>
                    你的成绩是:{{grade}}
                </div> -->
            </mu-dialog>

            
            <mu-dialog :open="gradedialog" title="成绩">
                你的成绩是{{grade}}分
                <!-- <mu-flat-button label="确定" slot="actions" primary @click="close"/> -->
            </mu-dialog>
        </mu-card>

            <mu-flexbox v-else :orient="`vertical`" :gutter="200" >
                <mu-flexbox-item>
                    
                </mu-flexbox-item>
                <mu-flexbox-item  justify="center" align="center">
                    <mu-circular-progress :size="80" :strokeWidth="5" color="#f50057"/>
                    <p>题目正在拼命加载中...</p>
                </mu-flexbox-item>
                <mu-flexbox-item >
                    
                </mu-flexbox-item>
            </mu-flexbox>


    </div>
</template>

<script>
import api from '../axios'
export default {
    data() {
        return {
            a: 1,
            value: 2,
            problems: [],
            loaded: false,
            curvalue: '',
            ans: [],
            snackbar: false,
            dialog: false,
            ZJUid: '',
            ZJUerror: false,
            submitted: false,
            grade: 0,
            gradedialog: false,
            snackbarmsg: ''
        }
    },
    created() {
        this.init();
    },
    methods: {
        init() {
            api.get_exam().then((res) => {
                this.problems = res.data;
                for(let i in this.problems) {
                    this.ans.push({
                        id: this.problems[i].id,
                        answer: ''
                    });
                }
                this.loaded = true;
                // console.log(this.problems);
            })
        },
        next() {
            if(!this.curvalue) {
                this.snackbarmsg = '不要急，先完成当前题目~'
                this.showSnackbar();
                return;
            }
            this.$router.push({ name: 'question', params: { id: Number(this.cur)+1 }})
        },
        prep() {
            // if(!this.curvalue) {
            //     this.showSnackbar();
            //     return;
            // }
            this.$router.push({ name: 'question', params: { id: Number(this.cur)-1 }})
        },
        showSnackbar () {
            this.snackbar = true
            if (this.snackTimer) clearTimeout(this.snackTimer)
            this.snackTimer = setTimeout(() => { this.snackbar = false }, 2000)
        },
        hideSnackbar () {
            this.snackbar = false
            if (this.snackTimer) clearTimeout(this.snackTimer)
        },
        open () {
            this.dialog = true
        },
        close () {
            this.dialog = false
        },
        open_final () {
            this.gradedialog = true
        },
        allsolve() {
            for (let i in this.ans) {
                if(!this.ans[i].answer) {
                    return false;
                }
            }
            return true;
        },
        show_submit() {
            if(!this.curvalue) {
                this.snackbarmsg = '不要急，先完成当前题目~';
                this.showSnackbar();
                return;
            }
            this.ans[this.cur-1].answer = this.curvalue;            
            if(!this.allsolve()) {
                this.snackbarmsg = '你还有题目未完成哦~去前面找找吧';
                this.showSnackbar();
                return;
            }
            this.open();
        },
        submit() {
            if(!this.ZJUid || isNaN(this.ZJUid)) {
                this.ZJUerror = true;
                return;
            }
            this.close();
            this.open_final();
            this.submitted = true;
            this.ZJUerror = false;
            this.ans[this.cur-1].answer = this.curvalue;
            const data = {
                ZJUid: this.ZJUid,
                ans: this.ans
            };
            // console.log(13);
            api.submit_exam(data).then((res) => {
                if(res.data.status === 'success') {
                    this.grade = parseFloat(res.data.correctcnt)*100/parseFloat(res.data.allcnt);
                }
            })
        }
    },
    computed: {
        cur() {
            return this.$route.params.id;
        }
    },
    watch: {
        $route(to, from) {
            // console.log(233);
            this.ans[from.params.id-1].answer = this.curvalue;
            // console.log(this.ans[from.params.id-1].answer);
            this.curvalue = this.ans[to.params.id-1].answer||'';
        },
    }
}
</script>

<style>
.center {
  position: fixed;
  z-index: 999;
}
</style>



